<template>
    <div>
        <h1> {{ article.title }}</h1>
        <div v-html="article.content" >
        </div>
    </div>
</template>

<script>
    export default {
        name: "Article",
        data() {
            return{
                id:this.$route.params.id,
                article:{
                    title:'',
                    content:''
                }
            }
        },
        mounted() {
            this.$axios.get("/novel/service/article/"+this.id).then(res=>{
                if (res.status == 200) {
                    this.article = res.data;
                }
            })
        }
    }
</script>

