<template>
    <div>
        <mt-button icon="more" @click="loadLatest()"></mt-button>
        <mt-cell
                v-for="item in articles"
                :key="item.id"
                :title="item.title"
                :to="'/article/'+item.id"
                is-link>
            <span v-show="item.see" style="color: green" class="mint-button-icon"><i class="mintui mintui-success"/></span>
        </mt-cell>
    </div>
</template>

<script>
    export default {
        name: "Book",
        data() {
            return{
                id:this.$route.params.id,
                book:{},
                articles:[]
            }
        },
        mounted() {
            this.loadArticle()
        },
        methods:{
            loadLatest() {
                this.$axios.get("/novel/service/book/load/"+this.id).then(res=>{
                    if (res.status == 200) {
                        this.loadArticle();
                    }
                })
            },
            loadArticle() {
                this.$axios.get("/novel/service/article/list/"+this.id).then(res=>{
                    if (res.status == 200) {
                        this.articles = res.data;
                    }
                })
            }
        }
    }
</script>

